.wrapper {
	position: relative;
}

.image {
	width: 100%;
	aspect-ratio: 16 / 9;
	
	border-radius: 10px;
	object-fit: cover;
}

.buttonContainer {
	position: absolute;
	display: flex;
	justify-content: space-between;
	
	
	inset: auto 0 50%;
	padding: 0 5%;
}

.button {
	background-color: black;
	border: none;
	color: white;
	padding: 10px 20px;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s ease;
}

.caption {
	margin-top: 10px;
	text-align: center;
	font-size: 1.2rem;
	font-weight: bold;
}