.project {
	margin-top: 100px;
	display: flex;
	flex-direction: column;
	gap: 3rem;
	font-size: 1.5rem;
	align-items: flex-start;
}
.name {
	font-size: 3rem;
	font-weight: bold;
	text-decoration: underline;
}

.header {
	font-size: 2rem;
}

.resume hr {
	width: 100%;
	border: 1px solid #000;
}

.button {
	margin-top: 20px;
	padding: 15px 30px;
	font-size: 1.5rem;
	background-color: black;
	color: white;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s ease;
	display: inline-block;
}